.box {
    background-color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 40%;
    box-shadow: 0 5px 15px rgb(0 0 0 / 20%);
    border-radius: 5px;
}

.review-img img {
    height: 140px;
    width: 140px;
    border-radius: 50%;
    object-fit: cover;
    position: absolute;
    top: 5px;
    left: -5px;
}

.review-img {
    background-color: #49a6e9;
    height: 140px;
    width: 140px;
    border-radius: 50%;
    position: relative;

}

.card {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin: 25px;


}

.lower-card {
    text-align: center;
    width: 90%;
    color: #49a6e9;
    display: flex;
    flex-direction: column;
}

.header {
    margin-bottom: 20px;
    color: #49a6e9;
    font-size: 40px;
    /* margin: 0px; */

    margin: 25px 0px 0px 0px;
}

.underline {
    height: 0.25rem;
    width: 6rem;
    background: #49a6e9;
    margin: 0px;
    margin: 3px 0px 45px 0px;

}

/* ------------btn------------- */
.btn {
    color: #49a6e9;
    background-color: white;
    border: 2px solid white;
    font-size: x-large;
    font-weight: 600;
    margin: 0px 15px 5px 15px;
    border-radius: 5px;
}

.surprice-btn {
    display: block;
    color: #49a6e9;
    background-color: #ebf7ff;
    border: 2px solid white;
    font-size: large;
    padding: 5px;
    margin: 15px;
    border-radius: 5px;

}

.surprice-btn:hover {
    background-color: #49a6e9;
    color: white;
}

.btn:hover {
    background-color: #49a6e9;
    color: white;
}

/* --------------------------- */
.name {
    margin: 30px 0px 5px 0px;
    font-size: 25px;
    text-transform: capitalize;
}

.job {
    margin: 0px;
    font-weight: none;
    text-transform: capitalize;
}

.text {
    color: #5783a2;
    font-size: 15px;

}

/* adding media query */
@media screen and (max-width: 1000px) {
    .box {
        width: -webkit-fill-available;
        margin: 15px;
    }
}